.container {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  overflow: auto;
}
.container > div {
  max-width: 200px;
  word-break: break-all;
}
.container .basic {
  height: 150px;
  width: 150px;
  border: 1px solid;
}
.container .line_1 {
  height: 150px;
  width: 150px;
  border: 1px solid;
  --my-length-1: 0px;
  background-image: repeating-linear-gradient(to right, transparent 0, transparent var(--my-length-1), pink var(--my-length-1) calc(30px - var(--my-length-1))), url('https://img1.baidu.com/it/u=1890390320,3399874998&fm=253&fmt=auto&app=120&f=JPEG?w=1422&h=800');
  background-size: auto,contain;
  -webkit-animation: lengthChange 2s infinite ease;
          animation: lengthChange 2s infinite ease;
}
@-webkit-keyframes lengthChange {
  0% {
    --my-length-1: 0px;
  }
  95% {
    --my-length-1: 0px;
  }
  100% {
    --my-length-1: 30px;
  }
}
@keyframes lengthChange {
  0% {
    --my-length-1: 0px;
  }
  95% {
    --my-length-1: 0px;
  }
  100% {
    --my-length-1: 30px;
  }
}
.container .line_2 {
  height: 150px;
  width: 150px;
  border: 1px solid;
  --my-length-1: 0px;
  background-image: repeating-linear-gradient(45deg, grey var(--my-length-1), grey calc(var(--my-length-1) + 10px), lightblue calc(var(--my-length-1) + 10px), lightblue calc(var(--my-length-1) + 20px));
  background-repeat: repeat;
  -webkit-animation: lengthChange 2s infinite linear;
          animation: lengthChange 2s infinite linear;
}
@keyframes lengthChange {
  0% {
    --my-length-1: 0px;
  }
  100% {
    --my-length-1: 20px;
  }
}
.container .line_3 {
  height: 150px;
  width: 150px;
  border: 1px solid;
  --my-length-1: 80%;
  --my-color-1: #c3ffff;
  background-image: linear-gradient(90deg, transparent 0, var(--my-color-1) var(--my-length-1), transparent 0);
  background-size: 150px 10px;
  background-repeat: no-repeat;
  background-position: left center;
  -webkit-animation: go_line3 infinite ease 5s;
          animation: go_line3 infinite ease 5s;
  border-color: var(--my-color-1);
}
@-webkit-keyframes go_line3 {
  from {
    --my-length-1: 0%;
    --my-color-1: #c3ffff;
  }
  to {
    --my-length-1: 100%;
    --my-color-1: red;
  }
}
@keyframes go_line3 {
  from {
    --my-length-1: 0%;
    --my-color-1: #c3ffff;
  }
  to {
    --my-length-1: 100%;
    --my-color-1: red;
  }
}
.container .line_4 {
  height: 150px;
  width: 150px;
  border: 1px solid;
  position: relative;
  background-image: linear-gradient(45deg, transparent 50%, red 50%, pink 70%, transparent 0);
  background-size: 50px 50px;
  background-position: center center;
  background-repeat: no-repeat;
}
.container .line_4::after {
  display: block;
  position: absolute;
  z-index: 100;
  content: '';
  width: 50px;
  height: 50px;
  border: none;
  background-image: linear-gradient(45deg, transparent 50%, black 50%, grey 70%, transparent 0);
  background-size: 50px 50px;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
  rotate: 90deg;
}
.container .line_5 {
  height: 150px;
  width: 150px;
  border: 1px solid;
  --my-color-1: #c3ffff;
  background-image: linear-gradient(45deg, transparent calc(var(--my-length-1) + 10%), var(--my-color-1) calc(var(--my-length-1) + 30%), #ffffff calc(var(--my-length-1) + 50%), transparent calc(var(--my-length-1) + 100%));
  --my-length-1: 0%;
  -webkit-animation: go_line5 infinite 1s ease;
          animation: go_line5 infinite 1s ease;
}
@-webkit-keyframes go_line5 {
  0% {
    --my-length-1: 0%;
    --my-color-1: #c3ffff;
  }
  100% {
    --my-length-1: 100%;
    --my-color-1: #eeff00;
  }
}
@keyframes go_line5 {
  0% {
    --my-length-1: 0%;
    --my-color-1: #c3ffff;
  }
  100% {
    --my-length-1: 100%;
    --my-color-1: #eeff00;
  }
}
.container .line_9 {
  width: 250px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border: 1px solid red;
  padding: 20px;
}
.container .line_9 span {
  background-image: linear-gradient(to right, black, transparent);
  background-size: 100% 0.2em;
  background-position: bottom;
  background-repeat-y: no-repeat;
}
.container .line_9 span:focus-visible {
  border: none;
  outline: none;
}
